import Image from "next/image";
import { AppointmentForm } from "@/components/forms/AppointmentForm";
import { getPatient } from "@/lib/actions/patient.actions";
import * as Sentry from "@sentry/nextjs";
const Appointment = async ({
  params: {
    userId
  }
}: SearchParamProps) => {
  const patient = await getPatient(userId);
  Sentry.metrics.set("user_view_new-appointment", patient.name);
  return <div className="flex h-screen max-h-screen" data-sentry-component="Appointment" data-sentry-source-file="page.tsx">
      <section className="remove-scrollbar container my-auto">
        <div className="sub-container max-w-[860px] flex-1 justify-between">
          <Image src="/medicures.png" height={1000} width={1000} alt="logo" className="mb-12 h-10 w-fit" data-sentry-element="Image" data-sentry-source-file="page.tsx" />

          <AppointmentForm patientId={patient?.$id} userId={userId} type="create" data-sentry-element="AppointmentForm" data-sentry-source-file="page.tsx" />

          <p className="copyright mt-10 py-12">© 2024 Medicures</p>
        </div>
      </section>

      <Image src="/assets/images/appointment-img.png" height={1500} width={1500} alt="appointment" className="side-img max-w-[390px] bg-bottom" data-sentry-element="Image" data-sentry-source-file="page.tsx" />
    </div>;
};
export default Appointment;